:root {
  --font-robo: "Roboto", sans-serif;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-middle: hsl(5, 77%, 71%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-card-hover: hsl(228, 25%, 27%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-text1: hsl(228, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-robo);
}

@keyframes MoveUpDown {
  0%, 100% {
    bottom: 0;
  }
  50% {
    bottom: 10px;
  }
}
@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 var(--dark-bg);
  }
}
h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 2.125rem;
  margin-bottom: 0.625rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 2.5rem;
  }
}

h2 {
  font-size: 1.5rem;
}

h4 {
  display: inline;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  height: 100vh;
  background-image: url("../images/Hogwarts.jpg");
  filter: grayscale(100%);
  background-size: cover;
  position: relative;
}
.header_next {
  position: absolute;
  top: 85%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.header_next-arrow {
  border: solid var(--light-bg);
  border-width: 0 4px 4px 0;
  display: inline-block;
  margin-top: 0.9375rem;
  padding: 7px;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  animation: MoveUpDown 1s linear infinite;
  position: relative;
}

.nav {
  display: flex;
  align-items: center;
}
.nav_title {
  margin-right: auto;
}
.nav_title > img {
  height: 6.25rem;
  width: 6.25rem;
}
.nav_links > ul {
  list-style: none;
  display: flex;
}
.nav_links > ul > li {
  margin-right: 1.25rem;
  transition: all 0.3s ease-in-out;
}
.nav_links > ul > li:hover {
  transform: scale(1.3);
  text-decoration: underline;
}

.toggle {
  display: grid;
  grid-template-columns: 1fr 3.75rem 1fr;
  grid-template-rows: repeat(2, 1fr);
  gap: 0.3rem 0.5rem;
  border: none;
  margin: 0;
}
.toggle label {
  font-size: 0.875rem;
  align-self: center;
}
.toggle label[for=dark] {
  grid-column: 1/2;
  justify-self: end;
}
.toggle label[for=system] {
  grid-column: 1/4;
  grid-row: 2/3;
  justify-self: center;
  align-self: start;
}
.toggle label[for=light] {
  grid-column: 3/4;
}
.toggle__wrapper {
  position: relative;
  grid-column: 2/3;
  grid-row: 1/2;
  height: 1.5rem;
}
.toggle input[type=radio] {
  margin: 0 -0.125rem 0 -0.125rem;
  opacity: 0;
  width: 1.25rem;
  height: 1.5rem;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 2px solid white;
}
.toggle__background {
  display: block;
  height: 100%;
  position: absolute;
  width: 100%;
  top: 0;
  border-radius: 0.75rem;
  background: linear-gradient(225deg, var(--toggle-bg-end) 0%, var(--toggle-bg-start) 98.02%);
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--dark-bg);
  transition: all 150ms ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 21px);
  right: 3px;
}
.toggle #system:checked ~ .toggle__button {
  left: 50%;
  right: auto;
  transform: translate(-50%);
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  position: relative;
  overflow: hidden;
  background: var(--dark-card);
  color: var(--dark-text1);
  padding: 1.5625rem;
  border-radius: 0.3125rem;
  text-align: center;
  transition: background 150ms ease-in-out;
  cursor: pointer;
}
.card:hover {
  background: var(--dark-card-hover);
}
.card--facebook {
  border-top: 0.3125rem solid var(--facebook);
}
.card--twitter {
  border-top: 0.3125rem solid var(--twitter);
}
.card--instagram {
  padding-top: 1.875rem;
}
.card--instagram::before {
  content: "";
  position: absolute;
  display: block;
  left: 0;
  top: 0;
  width: 100%;
  height: 0.3125rem;
  background: linear-gradient(225deg, var(--instagram-end), var(--instagram-middle) 50.91%, var(--instagram-start) 100%);
}
.card--youtube {
  border-top: 0.3125rem solid var(--youtube);
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 1.25rem;
  margin-top: 0.3125rem;
  margin-bottom: 1.75rem;
}
.card__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
}
.card__icon {
  margin-right: 0.5rem;
}
.card__username {
  font-size: 0.75rem;
  font-weight: 700;
}
.card__followers {
  margin-bottom: 1.5625rem;
}
.card__count {
  color: var(--light-bg);
  font-weight: 700;
  letter-spacing: -0.125rem;
  line-height: 1;
  margin-bottom: 0.25rem;
}
.card__count--big {
  font-size: 3.5rem;
}
.card__count--small {
  font-size: 2rem;
}
.card__label {
  font-size: 0.75rem;
  letter-spacing: 0.3125rem;
  font-weight: 400;
  color: var(--dark-text1);
  text-transform: uppercase;
}
.card__change {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.75rem;
  font-weight: 700;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--down {
  color: var(--brightred);
}
.card__change img {
  margin-right: 0.25rem;
}
.card-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  justify-items: start;
  gap: 1.4375rem;
}
.card-grid .card__count {
  margin-bottom: 0;
}
.card-grid .card__count,
.card-grid .card__change {
  align-self: end;
}
.card-grid img,
.card-grid .card__change {
  justify-self: end;
}

.main {
  height: 90vh;
  padding: 0.625rem;
  color: var(--light-bg);
  background-image: url("../images/BlackBackground.jpg");
  /* This container is needed to position the front and back side */
  /* Do an horizontal flip when you move the mouse over the flip box container */
  /* Position the front and back side */
  /* Style the front side (fallback if image is missing) */
  /* Style the back side */
}
.main_heading {
  font-size: 3.125rem;
}
.main-card {
  margin-top: 3.75rem;
  background-color: transparent;
  width: 18.75rem;
  height: 21.875rem;
  border-radius: 10px;
  overflow: hidden;
}
.main-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}
.main-card:hover .main-card-inner {
  transform: rotateY(180deg);
}
.main-card-front, .main-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  /* Safari */
  backface-visibility: hidden;
}
.main-card-front {
  background-color: var(--light-bg);
  color: var(--dark-bg);
}
.main-card-front-name {
  margin: 1.25rem;
  display: inline;
}
.main-card-front-rating {
  margin: 0;
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.main-card-front-rating > span:nth-child(1) {
  font-size: 1.5625rem;
  font-weight: 100;
}
.main-card-back {
  padding: 0.625rem;
  background-color: var(--light-card);
  color: var(--dark-top-bg);
  transform: rotateY(180deg);
}
.main-card-back-des {
  color: var(--light-text2);
  font-size: 0.75rem;
}
.main-card-back-date {
  display: flex;
  justify-content: space-between;
}
.main-card-back-tutor {
  margin: 0;
  width: 12.5rem;
  display: inline-flex;
  justify-content: space-between;
}
.main-card-back > button {
  margin-top: 0.625rem;
}

.button {
  background-color: var(--dark-bg);
  border: none;
  color: var(--light-bg);
  padding: 0.625rem 2.8125rem;
  transition: 0.25s;
  border-radius: 20px;
  font-size: 1.25rem;
  cursor: pointer;
}
.button:hover, .button:focus {
  background-color: var(--light-bg);
  color: var(--dark-bg);
  animation: pulse 1s;
  box-shadow: 0 0 0 2em rgba(255, 255, 255, 0);
}

.footer {
  height: 10vh;
  background-color: var(--dark-bg);
  color: var(--light-bg);
  display: flex;
  align-items: center;
  justify-content: center;
}